import defaultAvatar from '@/assets/default/avatar.png';
import meSideMenu from "@/components/meSideMenu";
import { ALL_JOB_RESUME, JOBS_APPLICATIONS, MY_JOBS, SET_APPLY_JOB_STATUS, SET_READ, USER_UNREAD } from '@/api/api';
import dashboardListsImg from '@/assets/dashboard/list.png';
import dashboardAdsImg from '@/assets/ads/2.png'; // import {onBeforeRouteUpdate} from "vue-router";

import { computed, ref } from "vue"; // import {encode} from "js-base64";

import { randomString } from "@/utils";
import { encode } from 'js-base64';
export default {
  name: "home",
  components: {
    meSideMenu
  },

  setup() {
    const i = ref(localStorage.getItem('identity'));
    const identity1 = computed(() => {
      return i.value;
    });
    return {
      identity1
    };
  },

  computed: {
    isThirdCompanyStatus: {
      get() {
        return this.$store.state.isThirdCompanyStatus;
      }

    },
    allIdentityChanged: {
      get() {
        return this.$store.state.allIdentityChanged;
      }

    }
  },

  data() {
    return {
      viewJobApplicantsChecked: [],
      viewApplicantsChecked: [],
      expandStatus: false,
      dashboardListsImg,
      dashboardAdsImg,
      defaultAvatar,
      userInfo: {},
      basicUserInfo: {},
      userContact: {},
      companyInfo: {},
      identity: localStorage.getItem('identity'),
      anotherUserId: 0,
      selectedApplicantsData: [],
      versionTime: randomString(),
      selectedJobId: 0,
      selectedApplicationId: 0,
      selectedApplicantStatus: false,
      sApplicantsData: [],
      filterByJobStatus: true,
      filterByApplicantStatus: false,
      myJobsData: [],
      myJobPage: 1,
      myJobLimit: 10,
      myJobTotalNum: 0,
      myApplicationsData: [],
      myApplicationsPage: 1,
      myApplicationsLimit: 10,
      myApplicationsTotalNum: 0,
      selectedApplicationIdWithCompany: 0
    };
  },

  mounted() {
    // let uid = localStorage.getItem('uid')
    if (this.identity == 1) {
      this.filterByApplicantStatus = true;
      this.filterByJobStatus = false;
    }

    if (this.filterByJobStatus) {
      this.getMyJobs(this.myJobPage, this.myJobLimit);
    }

    if (this.filterByApplicantStatus) {
      this.getAllJobResumeList(this.myApplicationsPage, this.myApplicationsLimit);
    }
  },

  methods: {
    viewEducatorDetail(info) {
      console.log(info);
      let companyId = info.user_contact.company_id;
      let userId = info.user_contact.id;
      let obj = {
        cid: companyId,
        uid: userId,
        identity: 1,
        from: 'other'
      };
      let str = encode(JSON.stringify(obj));
      let {
        href
      } = this.$router.resolve({
        path: '/educator/profile',
        query: {
          str: str
        }
      });
      window.open(href, '_blank'); // this.$router.push({path: '/educator/profile', query: {str: str}})
    },

    viewApplicationIdWithCompany(id) {
      this.selectedApplicationIdWithCompany = id;
    },

    getAllJobResumeList(page, limit) {
      let params = {
        token: localStorage.getItem('token'),
        page: page,
        limit: limit
      };
      ALL_JOB_RESUME(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.myApplicationsData = res.message.data; // console.log(res.message.data)

          this.myApplicationsTotalNum = res.message.total;
        }
      }).catch(err => {
        console.log(err);

        if (err.msg) {
          this.$message.error(err.msg);
        }

        if (err.message) {
          this.$message.error(err.message);
        }
      });
    },

    viewApplicationEvent(applicationId) {
      let index = this.viewApplicantsChecked.indexOf(applicationId);

      if (index == -1) {
        this.viewApplicantsChecked.push(applicationId);
      } else {
        this.viewApplicantsChecked.splice(index, 1);
      } // this.selectedApplicationId = applicationId
      // this.expandStatus = !this.expandStatus

    },

    editJob(jobId) {
      this.$router.push({
        path: '/jobs/post',
        query: {
          job_id: jobId
        }
      });
    },

    viewAllApplicants(jobId, unreadId, unreadStatus) {
      // this.selectedApplicantStatus = !this.selectedApplicantStatus;
      this.selectedJobId = jobId; // if (this.selectedApplicantStatus) {
      //
      // }

      if (unreadStatus) {
        let data = {
          id: unreadId,
          identity: localStorage.getItem('identity'),
          status: 1
        };
        SET_READ(data).then(res => {
          console.log(res);

          if (res.code == 200) {
            console.log('--------- set unread ----------- ');
          } else {
            console.log('set read:' + res.msg);
          }
        }).catch(err => {
          console.log(err);
        });
      }

      this.getJobResumes(jobId, 1, 100);
    },

    postJob() {
      this.$router.push({
        path: '/jobs/post',
        query: {
          version_time: this.versionTime
        }
      });
    },

    getMyJobs(page, limit) {
      let params = {
        token: localStorage.getItem('token'),
        page: page,
        limit: limit
      };
      MY_JOBS(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          let jobData = res.message.data;
          this.myJobTotalNum = res.message.total;
          let unread_data = {
            identity: localStorage.getItem('identity'),
            token: localStorage.getItem('token')
          };
          USER_UNREAD(unread_data).then(res => {
            if (res.code == 200) {
              let unreadListData = res.message.list;
              jobData.forEach(item => {
                // console.log(item)
                let a = unreadListData.filter(function (element) {
                  return element.type == 1 && element.type_id == item.id;
                });

                if (a.length > 0) {
                  item.unread_status = true;
                  item.unread_id = a[0].id;
                } else {
                  item.unread_status = false;
                }
              });
              this.myJobsData = jobData;
              console.log(jobData);
            } else {
              console.log('unread:' + res.msg);
            }
          });
        }
      }).catch(err => {
        console.log(err);
        this.$message.error(err.msg);
      });
    },

    getJobResumes(jobId, page, limit) {
      let params = {
        page: page,
        limit: limit,
        job_id: jobId
      };
      JOBS_APPLICATIONS(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.sApplicantsData = res.message.data; // console.log(res.message.data)
          // this.totalNum = res.message.total
        }
      }).catch(err => {
        console.log(err);
      });
    },

    filterByApplicants() {
      this.filterByJobStatus = false;
      this.filterByApplicantStatus = true;
      this.getAllJobResumeList(this.myApplicationsPage, this.myApplicationsLimit);
    },

    filterByJobs() {
      this.filterByJobStatus = true;
      this.filterByApplicantStatus = false;
      this.getMyJobs(this.myJobPage, this.myJobLimit);
    },

    myJobPageSizeChange(e) {
      console.log(e);
    },

    myJobPageChange(e) {
      console.log(e);
      this.myJobPage = e;
      this.getMyJobs(e, this.myJobLimit); // console.log(e)
      // document.documentElement.scrollTop = 120
    },

    myApplicationsPageSizeChange(e) {
      console.log(e);
    },

    myApplicationsPageChange(e) {
      console.log(e);
      this.myApplicationsPage = e;
      this.getAllJobResumeList(e, this.myApplicationsLimit); // console.log(e)
      // document.documentElement.scrollTop = 120
    },

    updateApplyJobStatus(id, value) {
      let params = {
        id: id,
        status: value
      };
      SET_APPLY_JOB_STATUS(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.$message({
            type: 'success',
            message: 'Success'
          });
        }
      }).catch(err => {
        console.log(err);
      });
    }

  }
};